.catalog__inner {
  .wrap;
  padding: 0;
  font-size: 12px;
  line-height: 16px;
  padding-top: 15px;
  margin-bottom: 30px;
}

.catalog__title {
  .reset;
  font-family: "Oswald", "Arial", sans-serif;
  font-size: 36px;
  line-height: 60px;
  font-weight: 400;
  color: @title-color;
  padding: 0 20px 27px 20px;
  border-bottom: 1px solid @border-color;
}

.catalog__list {
  .list-reset;
}

@media (min-width: @tablet-width) {
  .catalog__inner {
    padding: 30px 30px 0;
    display: flex;
    flex-direction: column;
    margin-bottom: 66px;
  }

  .catalog__title {
    font-size: 60px;
    text-align: left;
    border: none;
    padding-left: 0;
    padding-bottom: 27px;
  }

  .catalog__list {
    display: flex;
    justify-content: space-between;
    flex-wrap: wrap;
    align-items: stretch;
  }

  .catalog__item {
    width: 324px;
    display: flex;
    margin-top: 139px;
  }
}

@media (min-width: @desktop-width) {
  .catalog__inner {
    padding: 16px 40px 0;
    margin-bottom: 76px;
  }

  .catalog__title {
    padding-bottom: 22px;
  }

  .catalog__item {
    width: 245px;
    margin-top: 141px;
  }
}
