  margin: 0;
  padding: 0;
  list-style: none;
}
.button {
  display: block;
  width: 280px;
  background-color: #68b738;
  color: #ffffff;
  border: none;
  font-family: "Oswald", "Arial", sans-serif;
  font-weight: 400;
  font-size: 16px;
  line-height: 20px;
  text-transform: uppercase;
  text-align: center;
  padding: 10px 0;
}
