.page-footer {
  background-color: @grey-color;
}

.page-footer__inner {
  .wrap;
  padding: 0;
  display: flex;
  flex-direction: column;
  justify-content: center;
}

.page-footer__logo {
  display: block;
  border-bottom: 1px solid @border-color;
  padding-bottom: 27px;
  padding-top: 38px;
  text-align: center;

  &:hover,
  &:focus {
    opacity: 0.8;
  }

  &:active {
    opacity: 0.6;
  }
}

.page-footer__logo-icon {
  fill: @title-color;
}

.page-footer__logo-image {
  display: block;
  margin: 0 auto;
}

@media (min-width: @tablet-width) {
  .page-footer__inner {
    padding: 51px 20px 51px 20px;
    flex-direction: row;
    justify-content: space-between;
    align-items: center;
  }

  .page-footer__social {
    border: none;
  }

  .page-footer__logo {
    width: 165px;
    border: none;
    padding: 0;
    padding-left: 10px;
    text-align: left;
  }
}

@media (min-width: @desktop-width) {
  .page-footer__inner {
    padding: 51px 37px 51px 30px;
    flex-direction: row;
    justify-content: space-between;
  }
}
